@use 'header';
@use 'hero';
@use 'tabs';
@use 'footer';

*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

ul {
    list-style: none;
}

body {
    background-color: #00050d;
    padding-bottom: 30px;
    font-family: Roboto, sans-serif;
}

section, footer {
    padding: 0 calc(3.5vw + 24px);
}
